@import url('https://fonts.googleapis.com/css2?family=DynaPuff&family=Josefin+Sans&family=Silkscreen&display=swap');


*{
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
}

body {
  background-color: #edeef0;
}

.header {
  display: none;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 1535px;
  height: 60px;
  font-size: 20px;
  color: white;
  background-color: #909fa6;
  border-bottom: 1px solid rgb(233, 233, 233);
}

.nowTime {
  display: flex;
  font-size: 36px;
  justify-content: flex-start;
  margin-top: 305px;
  margin-left: 22px;
}

.allContent {
  display: flex;
  justify-content: space-between;
  
}

.leftNav {
  padding-top: 20px;
  bottom: 60px;
  border-radius: 20px;
  margin-left: 20px;
  position: fixed;
  width: 250px;
  height: 530px;
  background-color: #d1dfe8;
}

.auto {
  overflow: auto;
}

.leftNav a {
  text-decoration: none;
  color:rgb(11, 23, 35);
  font-size: 22px;
}

.leftNavLink {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 230px;
  margin-left: 40px;
}

.todayContent {
  margin-left: 50px;
}

.todayMainContent {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 21px;
  width: 670px;
  height: 120px;
  padding-left: 20px;
  color: #424e6a;
  background-color: whitesmoke;
  border-radius: 20px;
}

.quotes span:first-child{
  margin-right: 20px;

}

.mainAndtimer {
  display: flex;
  justify-content: space-between;
  width: 1180px;
  margin-bottom: 50px;
}

.homeTimer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 470px;
  height: 160px;
  padding-top: 20px;
  border-radius: 15px;
  background-color:#ede1e3;
}

.displayMessage {
  margin-bottom: 15px;
  color: white;
  font-size: 20px;
  text-align: center;
}

.todoContent {
  margin-left: 265px;
  margin-top: 110px;
}

.todayContent h1 {
  margin-bottom: 15px;
}

.textList {
  margin-top: 30px;
  
}
.textList2 {
  margin-left: 50px;
}

.textList p {
  font-size: 20px;
}

.inputButton {
  margin-top: 10px;
  margin-bottom: 30px;
}

.inputButton2 {
  margin-bottom: 30px;
}

.insertBox {
  width: 1160px;
  height: 45px;
  font-size: 15px;
  border: 0;
  border-radius: 10px;
  outline: none;
  margin-top: 10px;
  padding-left: 20px;
  background-color: lightgray;
}

.todoList {
  display: flex;
  align-items: center;
  width: 1100px;
  height: 40px;
  font-size: 15px;
  border: 0;
  border-radius: 10px;
  outline: none;
  margin-bottom: 10px;
  padding-left: 20px;
  background-color: rgb(233, 233, 233);
}

body {
  overflow: scroll;
}

body::-webkit-scrollbar {
  width: 10px;
  height: 2px;
}

body::-webkit-scrollbar-track {
  background-color: transparent;
}
body::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color:#909fa6;
}
body::-webkit-scrollbar-button {
  width: 0;
  height: 0;
}

/* Timer Component */
.timer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.minAndsec {
  font-size: 70px;
  color: #424e6a;

  margin-bottom: 15px;
}

.timerBtns {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 330px;
}

.timerBtns button {
  font-size: 15px;
  height: 40px;
  width: 100px;
  border: none;
  border-radius: 10px;
  transition-duration: 0.4s;
  cursor: pointer;
  margin-bottom: 20px;
}

.timerBtns button:hover {
  background-color: #2c4856; /* Green */
  color: white;
}


/* TodoListItem Component */

.checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-right: 10px;
  
}

svg {
  width: 23px;
  height: 23px;
}

.removebox{
  display: flex;
  cursor: pointer;
}

.todolistitemStyle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1139px;
  height: 45px;
  padding: 0 20px;
  margin-bottom: 15px;
  font-size: 20px;
  background-color: whitesmoke;
  border-radius: 10px;
}

.checkboxAndcontent {
  display: flex;
  align-items: center;
}

.line {
  color: gray;
  text-decoration: line-through;
}

.noline {
  color: black;
}

/* Calender Component */




/* Edit function */

.editedInput {
  width: 970px;
  font-size: 20px;
  border: 0;
  outline: none;
  background-color: #Efeff1;
}

@media screen and (max-width: 768px) {

  .header {
    position: fixed;
    width: 100%;
  }

  /*Left Navigation Bar*/
  .leftNav {
    position: fixed;
    margin-left: 0;
    width: 100%;
    height: 40px;
    bottom: 0px;
    padding-top: 20px;
    border-radius: 0px;
    background-color: #edeef0;
    border-top-style: solid;
    border-top-color: #bdbebd;
    border-top-width: 0.4px;
    
  }

  .leftNav a {
    font-size: 17px;
  }

  .leftNavLink {
    position: relative;
    flex-direction: row;
    margin-left: 0px;
    height: 0px;
  }

  .todayContent h1 {
    margin-bottom: 0;
  }
  .quotes {
    display: none;
  }

  .nowTime {
    display: none;
  }

  .todoContent {
    padding: 100px 30px 0 30px;
    margin: 0;
  }

  .todayContent {
    margin-left: 0px;
  }

  .textList2 {
    margin-left: 0px;
  }

  /*Timer*/

  .timer {
    flex-direction: row;

  }

  .mainAndtimer {
    width: 100%;
    flex-direction: column;
  }

  .homeTimer {
    width: 100%;
    height: 50px;
    padding: 10px 0;

  }

  .minAndsec {
    margin: 7px 30px 0 0;
    font-size: 40px;
  }

  .timerBtns {
    width: 100px;
  }

  .timerBtns button {
    margin-bottom: 0px;
  }

  .todayMainContent {
    background-color: #edeef0;
    width: 100%;
    height: 80px;
    padding-left: 0px;
    font-size: 20px;
    border-radius: 0px;
  }

  .insertBox {
    width: 100%;
    box-sizing: border-box;
  }

  .todolistitemStyle {
    width: 100%;
    height: 40px;
    box-sizing: border-box;
  }

  .checkbox svg{
    width: 20px;
    height: 20px;
 }

 .removebox {
  width: 20px;
  height: 20px;
}

/*Scroll Bar*/
body::-webkit-scrollbar {
  width: 5px;
  height: 2px;
}

}

